	module.exports = {"data":{"markdownRemark":{"html":"<p><strong><em>John Coltrane</em></strong><br>\n<em>Lush Life</em><br>\nI love Coltrane. His soloing pulls so far away from the relatively staid rhythm section. Occasionally they pull together some syncopated phrasing too.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/records-i-bought/static/coltrane-lush-life-56c95aece6a86ccb17c0667a35959cde-8e2e9.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; margin-top:10px;; max-width: 300px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"coltrane-lush-life\"\n        title=\"\"\n        src=\"/records-i-bought/static/coltrane-lush-life-56c95aece6a86ccb17c0667a35959cde-8e2e9.jpg\"\n        srcset=\"/records-i-bought/static/coltrane-lush-life-56c95aece6a86ccb17c0667a35959cde-dd96d.jpg 148w,\n/records-i-bought/static/coltrane-lush-life-56c95aece6a86ccb17c0667a35959cde-eaf39.jpg 295w,\n/records-i-bought/static/coltrane-lush-life-56c95aece6a86ccb17c0667a35959cde-8e2e9.jpg 300w\"\n        sizes=\"(max-width: 300px) 100vw, 300px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><strong><em>Yumi Zouma</em></strong><br>\n<em>EP III</em><br>\nMajestic 80s power pop sounds massaged into some beautiful love songs. I played this so many times off Spotify that I realized it's an important record to me. It's very touching and danceable and warm.\n<img src=\"zouma-ep-iii.jpg\" alt=\"zouma-ep-iii\"></p>\n<p><strong><em>Lexaunculpt</em></strong><br>\n<em>Oh Here's Some Noises</em><br>\nOh shit I may already have a copy of this record. I was listening passively to side A because it was just some crunchy IDM beats and then a really emotional set of strings and noises got my attention. It's really devoted to weird sounds and emotions when it wants to be. It's from 1998!\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/records-i-bought/static/lexaunculpt-some-noises-396cc0ee405210d6258a374163398570-8e2e9.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; margin-top:10px;; max-width: 300px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"lexaunculpt-some-noises\"\n        title=\"\"\n        src=\"/records-i-bought/static/lexaunculpt-some-noises-396cc0ee405210d6258a374163398570-8e2e9.jpg\"\n        srcset=\"/records-i-bought/static/lexaunculpt-some-noises-396cc0ee405210d6258a374163398570-dd96d.jpg 148w,\n/records-i-bought/static/lexaunculpt-some-noises-396cc0ee405210d6258a374163398570-eaf39.jpg 295w,\n/records-i-bought/static/lexaunculpt-some-noises-396cc0ee405210d6258a374163398570-8e2e9.jpg 300w\"\n        sizes=\"(max-width: 300px) 100vw, 300px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"date":"November 27, 2018","path":"/coltrane-zouma-lexaunculpt","title":"John Coltrane, Yumi Zouma, and Lexaunculpt"}}},"pathContext":{}}

